const ONE = 'ONE';
const TWO = 'TWO';
const THREE = 'THREE';
const FOUR = 'FOUR';
const FIVE = 'FIVE';
const SIX = 'SIX';
const SEVEN = 'SEVEN';
const EIGHT = 'EIGHT';
const NINE = 'NINE';
const DOT = 'DOT';
const ZERO = 'ZERO';
const PLUS = 'PLUS';
const MINUS = 'MINUS';
const TIMES = 'TIMES';
const DIVIDED = 'DIVIDED';
const EQUALS = 'EQUALS';
const CLEAR = 'CLEAR';
const AOVER = 'AOVER';
const POVER = 'POVER';
const BOTHOVER = 'BOTHOVER';
const MANUAL = 'MANUAL';
const DEL = 'DEL';

export const oneA = () => ({
    type: ONE
});

export const twoA = () => ({
    type: TWO
});

export const threeA = () => ({
    type: THREE
});

export const fourA = () => ({
    type: FOUR
});

export const fiveA = () => ({
    type: FIVE
});

export const sixA = () => ({
    type: SIX
});

export const sevenA = () => ({
    type: SEVEN
});

export const eightA = () => ({
    type: EIGHT
});

export const nineA = () => ({
    type: NINE
});

export const dotA = () => ({
    type: DOT
});

export const zeroA = () => ({
    type: ZERO
});

export const plusA = () => ({
    type: PLUS
});

export const minusA = () => ({
    type: MINUS
});

export const timesA = () => ({
    type: TIMES
});

export const dividedA = () => ({
    type: DIVIDED
});

export const equalsA = (func) => ({
    type: EQUALS,
    payload: func
});

export const clearA = () => ({
    type: CLEAR
});

export const aOverA = () => ({
    type: AOVER
});

export const pOverA = () => ({
    type: POVER
});

export const bothOverA = () => ({
    type: BOTHOVER
});

export const manualA = (obj) => ({
    type: MANUAL,
    payload: obj
});

export const delA = () => ({
    type: DEL
})