const state1 = {
    active: '0',
    passive: ''
}

const characterReducer = (state = state1, action) => {
    if (state.active === "NUM LIMIT REACHED" || state.passive === "NUM LIMIT REACHED") {
        switch (action.type) {
            case 'MANUAL':
                return action.payload;
            case 'CLEAR':
                return {active: "0", passive: ""};
            default:
                return state;
        }
    } else if (state.active.length >= 21 && state.passive.length >= 23) {
        switch (action.type) {
            case 'BOTHOVER':
                return { active: "NUM LIMIT REACHED", passive: "" }
            case 'MANUAL':
                return action.payload;
            case 'CLEAR':
                return { active: '0', passive: '' };
            case 'EQUALS':
                const result = action.payload(state.passive.concat(state.active));
                return { active: result, passive: '=' };
            case 'DEL':
                return { active: state.active.slice(0, state.active.length - 1), passive: state.passive}
            default:
                return state;
        }
    } else if (state.active.length >= 21) {
        switch (action.type) {
            case 'AOVER':
                return { active: "NUM LIMIT REACHED", passive: state.passive };
            case 'MANUAL':
                return action.payload;
            case 'CLEAR':
                return { active: '0', passive: '' };
            case 'EQUALS':
                const result = action.payload(state.passive.concat(state.active));
                return { active: result, passive: '=' };
            case 'DEL':
                return { active: state.active.slice(0, state.active.length - 1), passive: state.passive};
            default:
                return state;
        }
    } else if (state.passive.length >= 23) {
        switch (action.type) {
            case 'POVER':
                return { active: state.active, passive: "NUM LIMIT REACHED" };
            case 'MANUAL':
                return action.payload;
            case 'CLEAR':
                return { active: '0', passive: '' };
            case 'EQUALS':
                const result = action.payload(state.passive.concat(state.active));
                return { active: result, passive: '=' };
                case 'DEL':
                    return { active: state.active.slice(0, state.active.length - 1), passive: state.passive};
            default:
                return state;
        }
    } else if (state.active === '0' && state.passive.length < 1) {
        switch (action.type) {
            case 'ONE':
                return { active: '1', passive: state.passive };
            case 'TWO':
                return { active: '2', passive: state.passive };
            case 'THREE':
                return { active: '3', passive: state.passive };
            case 'FOUR':
                return { active: '4', passive: state.passive };
            case 'FIVE':
                return { active: '5', passive: state.passive };
            case 'SIX':
                return { active: '6', passive: state.passive };
            case 'SEVEN':
                return { active: '7', passive: state.passive };
            case 'EIGHT':
                return { active: '8', passive: state.passive };
            case 'NINE':
                return { active: '9', passive: state.passive };
            case 'DOT':
                return { active: '.', passive: state.passive }
            case 'ZERO':
                return { active: '0', passive: state.passive };
            case 'PLUS':
                return { active: '+', passive: '0+' };
            case 'MINUS':
                return { active: '-', passive: '0-' };
            case 'TIMES':
                return { active: '*', passive: '0*' };
            case 'DIVIDED':
                return { active: '/', passive: '0/' };
            case 'EQUALS':
                return state;
            case 'CLEAR':
                return { active: '0', passive: '' };
            case 'DEL':
                return state;
            default:
                return state;
        }
    } else if (state.active === "+" || state.active === "-" || state.active === "*" || state.active === "/") {
        switch (action.type) {
            case 'ONE':
                return { active: '1', passive: state.passive };
            case 'TWO':
                return { active: '2', passive: state.passive };
            case 'THREE':
                return { active: '3', passive: state.passive };
            case 'FOUR':
                return { active: '4', passive: state.passive };
            case 'FIVE':
                return { active: '5', passive: state.passive };
            case 'SIX':
                return { active: '6', passive: state.passive };
            case 'SEVEN':
                return { active: '7', passive: state.passive };
            case 'EIGHT':
                return { active: '8', passive: state.passive };
            case 'NINE':
                return { active: '9', passive: state.passive };
            case 'DOT':
                return { active: '.', passive: state.passive }
            case 'ZERO':
                return { active: '0', passive: state.passive };
            case 'PLUS':
                return { active: '+', passive: state.passive.slice(0, state.passive.length - 1).concat("+") };
            case 'MINUS':
                return { active: '-', passive: state.passive.slice(0, state.passive.length - 1).concat("-") };
            case 'TIMES':
                return { active: '*', passive: state.passive.slice(0, state.passive.length - 1).concat("*") };
            case 'DIVIDED':
                return { active: '/', passive: state.passive.slice(0, state.passive.length - 1).concat("/") };
            case 'EQUALS':
                if (state.passive.length > 0) {
                    return { active: action.payload(state.passive), passive: state.passive.concat("=") }
                } else {
                    return state;
                }
            case 'CLEAR':
                return { active: '0', passive: '' };
            case 'DEL':
                return state;
            default:
                return state;
        }
    } else if (state.passive[state.passive.length - 1] === "=") {
        switch (action.type) {
            case 'ONE':
                return { active: "1", passive: "" };
            case 'TWO':
                return { active: "2", passive: "" };
            case 'THREE':
                return { active: "3", passive: "" };
            case 'FOUR':
                return { active: "4", passive: "" };
            case 'FIVE':
                return { active: "5", passive: "" };
            case 'SIX':
                return { active: "6", passive: "" };
            case 'SEVEN':
                return { active: "7", passive: "" };
            case 'EIGHT':
                return { active: "8", passive: "" };
            case 'NINE':
                return { active: "9", passive: "" };
            case 'DOT':
                return { active: ".", passive: "" }
            case 'ZERO':
                return { active: "0", passive: "" };
            case 'PLUS':
                return { active: '+', passive: state.active.concat("+") };
            case 'MINUS':
                return { active: '-', passive: state.active.concat("-") };
            case 'TIMES':
                return { active: '*', passive: state.active.concat("*") };
            case 'DIVIDED':
                return { active: '/', passive: state.active.concat("/") };
            case 'EQUALS':
                return state;
            case 'CLEAR':
                return { active: '0', passive: '' };
            case 'POVER':
                return { active: state.active, passive: "NUM LIMIT REACHED" };
            case 'AOVER':
                return { active: "NUM LIMIT REACHED", passive: state.passive };
            case 'BOTHOVER':
                return { active: "NUM LIMIT REACHED", passive: "" };
            case 'MANUAL':
                return action.payload;
            case 'DEL':
                return state;
            default:
                return state;
        }
    } else if (state.active === "0") {
        switch (action.type) {
            case 'ONE':
                return { active: '1', passive: state.passive };
            case 'TWO':
                return { active: '2', passive: state.passive };
            case 'THREE':
                return { active: '3', passive: state.passive };
            case 'FOUR':
                return { active: '4', passive: state.passive };
            case 'FIVE':
                return { active: '5', passive: state.passive };
            case 'SIX':
                return { active: '6', passive: state.passive };
            case 'SEVEN':
                return { active: '7', passive: state.passive };
            case 'EIGHT':
                return { active: '8', passive: state.passive };
            case 'NINE':
                return { active: '9', passive: state.passive };
            case 'DOT':
                return { active: '.', passive: state.passive }
            case 'ZERO':
                return { active: '0', passive: state.passive };
            case 'PLUS':
                return { active: '+', passive: '0+' };
            case 'MINUS':
                return { active: '-', passive: '0-' };
            case 'TIMES':
                return { active: '*', passive: '0*' };
            case 'DIVIDED':
                return { active: '/', passive: '0/' };
            case 'EQUALS':
                return state;
            case 'CLEAR':
                return { active: '0', passive: '' };
            case 'POVER':
                return { active: state.active, passive: "NUM LIMIT REACHED" };
            case 'MANUAL':
                return action.payload;
            case 'DEL':
                return state;
            default:
                return state;
        }
    } else {
        switch (action.type) {
            case 'ONE':
                return { active: state.active.concat("1"), passive: state.passive };
            case 'TWO':
                return { active: state.active.concat("2"), passive: state.passive };
            case 'THREE':
                return { active: state.active.concat("3"), passive: state.passive };
            case 'FOUR':
                return { active: state.active.concat("4"), passive: state.passive };
            case 'FIVE':
                return { active: state.active.concat("5"), passive: state.passive };
            case 'SIX':
                return { active: state.active.concat("6"), passive: state.passive };
            case 'SEVEN':
                return { active: state.active.concat("7"), passive: state.passive };
            case 'EIGHT':
                return { active: state.active.concat("8"), passive: state.passive };
            case 'NINE':
                return { active: state.active.concat("9"), passive: state.passive };
            case 'DOT':
                if (/\./.test(state.active)) {
                    return state;
                } else {
                    return { active: state.active.concat("."), passive: state.passive }
                }
            case 'ZERO':
                return { active: state.active.concat("0"), passive: state.passive };
            case 'PLUS':
                return { active: '+', passive: state.passive.concat(state.active, "+") };
            case 'MINUS':
                return { active: '-', passive: state.passive.concat(state.active, "-") };
            case 'TIMES':
                return { active: '*', passive: state.passive.concat(state.active, "*") };
            case 'DIVIDED':
                return { active: '/', passive: state.passive.concat(state.active, "/") };
            case 'EQUALS':
                const finalStr = state.passive.concat(state.active);
                return { active: action.payload(finalStr), passive: state.passive.concat(state.active, "=") };
            case 'CLEAR':
                return { active: '0', passive: '' };
            case 'POVER':
                return { active: state.active, passive: "NUM LIMIT REACHED" };
            case 'AOVER':
                return { active: "NUM LIMIT REACHED", passive: state.passive };
            case 'BOTHOVER':
                return { active: "NUM LIMIT REACHED", passive: "" };
            case 'MANUAL':
                return action.payload;
            case 'DEL':
                return { active: state.active.slice(0, state.active.length - 1), passive: state.passive};
            default:
                return state;
        }
    }
}

export default characterReducer;