import React from 'react';
import Display from './components/Display';
import { Component } from 'react';
import * as characters from './redux/actions/characters';
import { connect } from 'react-redux';
import formula from './logic/formula';
import immediate from './logic/immediate';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      modeClass: "i"
    }
  }

  componentDidMount = () => {
    document.addEventListener("keydown", this.handlePress);
  }

  componentWillUnmount = () => {
    document.removeEventListener("keydown", this.handlePress);
  }
  handlePress = (e) => {
    if (e.keyCode === 8 || e.keyCode === 46) {
      this.props.del();
    } else if (e.keyCode === 27) {
      this.props.clear();
    } else if (this.props.character.active.length < 21 && this.props.character.passive.length < 23) {
    switch (e.keyCode) {
      case 49:
        this.props.one();
        break;
      case 97:
        this.props.one();
        break;
      case 50:
        this.props.two();
        break;
      case 98:
        this.props.two();
        break;
      case 51:
        this.props.three();
        break;
      case 99:
        this.props.three();
        break;
      case 52:
        this.props.four();
        break;
      case 100:
        this.props.four();
        break;
      case 53:
        this.props.five();
        break;
      case 101:
        this.props.five();
        break
      case 54:
        this.props.six();
        break;
      case 102:
        this.props.six();
        break;
      case 55:
        this.props.seven();
        break;
      case 103:
        this.props.seven();
        break;
      case 56:
        this.props.eight();
        break;
      case 104:
        this.props.eight();
        break;
      case 57:
        this.props.nine();
        break;
      case 105:
        this.props.nine();
        break;
      case 48:
        this.props.zero();
        break;
      case 96:
        this.props.zero();
        break;
      case 110:
        this.props.dot();
        break;
      case 190:
        this.props.dot();
        break;
      case 187:
        this.props.plus();
        break;
      case 107:
        this.props.plus();
        break;
      case 109:
        this.props.minus();
        break;
      case 189:
        this.props.minus();
        break;
      case 106:
        this.props.times();
        break;
      case 88:
        this.props.times();
        break;
      case 111:
        this.props.divided();
        break;
      case 191:
        this.props.divide();
        break;
      case 13:
        if(this.props.mode.mode === "immediate") {
          this.props.equals(immediate);
        } else if (this.props.mode.mode === "formula") {
          this.props.equals(formula);
        }
        break;
      default:
        return;
    }
  } else if (this.props.character.active.length >= 21 && this.props.character.passive.length >= 23) {
    const keep = this.props.character;
    const manual = this.props.manual;
    this.props.bothover();
    setTimeout(function(){
      manual(keep);
    }, 3000);

  } else if (this.props.character.active.length >= 21) {
    const keep = this.props.character;
    const manual = this.props.manual;
    this.props.aover();
    setTimeout(function(){
      manual(keep);
    }, 3000);
  } else {
    const keep = this.props.character;
    const manual = this.props.manual;
    this.props.pover();
    setTimeout(function(){
      manual(keep);
    }, 3000);
  } 
  }

  handleClick = (e) => {
    if (e.target.name === "del") {
      this.props.del();
    } else if (e.target.name === "clear") {
      this.props.clear();
    } else if (this.props.character.active.length < 21 && this.props.character.passive.length < 23) {
    switch (e.target.name) {
      case "1":
        this.props.one();
        break;
      case "2":
        this.props.two();
        break;
      case "3":
        this.props.three();
        break;
      case "4":
        this.props.four();
        break;
      case "5":
        this.props.five();
        break;
      case "6":
        this.props.six();
        break;
      case "7":
        this.props.seven();
        break;
      case "8":
        this.props.eight();
        break;
      case "9":
        this.props.nine();
        break;
      case "0":
        this.props.zero();
        break;
      case "dot":
        this.props.dot();
        break;
      case "plus":
        this.props.plus();
        break;
      case "minus":
        this.props.minus();
        break;
      case "times":
        this.props.times();
        break;
      case "divided-by":
        this.props.divided();
        break;
      case "equals":
        if(this.props.mode.mode === "immediate") {
          this.props.equals(immediate);
        } else if (this.props.mode.mode === "formula") {
          this.props.equals(formula);
        }
        break;
      default:
        return;
    }
  } else if (this.props.character.active.length >= 21 && this.props.character.passive.length >= 23) {
    const keep = this.props.character;
    const manual = this.props.manual;
    this.props.bothover();
    setTimeout(function(){
      manual(keep);
    }, 3000);

  } else if (this.props.character.active.length >= 21) {
    const keep = this.props.character;
    const manual = this.props.manual;
    this.props.aover();
    setTimeout(function(){
      manual(keep);
    }, 3000);
  } else {
    const keep = this.props.character;
    const manual = this.props.manual;
    this.props.pover();
    setTimeout(function(){
      manual(keep);
    }, 3000);
  } 
  }

  changeMode = (e) => {
    const mode = e.target.id;
    this.setState({
      modeClass: mode
    })
  }

  render() {
    const classN = this.state.modeClass;
    return (
      <main id="calculator">
        <Display change={this.changeMode} />
        <button type="button" onClick={this.handleClick} id="one" name="1" className={classN}>1</button>
        <button type="button" onClick={this.handleClick} id="two" name="2" className={classN}>2</button>
        <button type="button" onClick={this.handleClick} id="three" name="3" className={classN}>3</button>
        <button type="button" onClick={this.handleClick} id="four" name="4" className={classN}>4</button>
        <button type="button" onClick={this.handleClick} id="five" name="5" className={classN}>5</button>
        <button type="button" onClick={this.handleClick} id="six" name="6" className={classN}>6</button>
        <button type="button" onClick={this.handleClick} id="seven" name="7" className={classN}>7</button>
        <button type="button" onClick={this.handleClick} id="eight" name="8" className={classN}>8</button>
        <button type="button" onClick={this.handleClick} id="nine" name="9" className={classN}>9</button>
        <button type="button" onClick={this.handleClick} id="zero" name="0" className={classN}>0</button>
        <button type="button" onClick={this.handleClick} id="equals" name="equals" className={classN}>=</button>
        <button type="button" onClick={this.handleClick} id="decimal" name="dot" className={classN}>.</button>
        <button type="button" onClick={this.handleClick} id="clear" name="clear" className={classN}>AC</button>
        <button type="button" onClick={this.handleClick} id="add" name="plus" className={classN}>+</button>
        <button type="button" onClick={this.handleClick} id="subtract" name="minus" className={classN}>-</button>
        <button type="button" onClick={this.handleClick} id="multiply" name="times" className={classN}>*</button>
        <button type="button" onClick={this.handleClick} id="divide" name="divided-by" className={classN}>/</button>
        <button type="button" onClick={this.handleClick} id="delete" name="del" className={classN}>Del</button>
      </main>

    );
  }
}

const mapStateToProps = (state) => ({
  character: state.character,
  mode: state.mode
});

const characterActions = {
  one: characters.oneA,
  two: characters.twoA,
  three: characters.threeA,
  four: characters.fourA,
  five: characters.fiveA,
  six: characters.sixA,
  seven: characters.sevenA,
  eight: characters.eightA,
  nine: characters.nineA,
  zero: characters.zeroA,
  dot: characters.dotA,
  plus: characters.plusA,
  minus: characters.minusA,
  times: characters.timesA,
  divided: characters.dividedA,
  equals: characters.equalsA,
  clear: characters.clearA,
  aover: characters.aOverA,
  pover: characters.pOverA,
  bothover: characters.bothOverA,
  manual: characters.manualA,
  del: characters.delA
}

export default connect(mapStateToProps, characterActions)(App);

