import React, { Component } from 'react';
import { connect } from 'react-redux';
import Mode from './Mode';
import propTypes from 'prop-types';

class Display extends Component {

    render() {

        return (
            <div id="display-bar">
                    <Mode change={this.props.change} />
                <div id="display-group">
                    <div id="passive-display">
                        <p>{this.props.passive}</p>
                    </div>
                    <div id="display">
                        <p>{this.props.active}</p>
                    </div>
                </div>
            </div>
        )
    }
}

Display.propTypes = {change: propTypes.func.isRequired};


const mapStateToProps = (state) => ({
    active: state.character.active,
    passive: state.character.passive
})


export default connect(mapStateToProps)(Display);
