let formula = (str) => {
    let despace = (str) => {
        return str.replace(/\s+/g, "");
    };

    let mdSolver = (str) => {
        const reg = /^(-{0,1}\d*\.{0,1}\d+)([*/])(-{0,1}\d*\.{0,1}\d+)$/;
        const matchArr = str.match(reg);
        const firstNum = parseFloat(matchArr[1]);
        const op = matchArr[2];
        const secondNum = parseFloat(matchArr[3]);
        switch(op) {
            case "*":
                return firstNum * secondNum;
            case "/":
                return firstNum / secondNum;
            default:
                return NaN;
        }
    }

    let asSolver = (str) => {
        const reg = /^(-{0,1}\d*\.{0,1}\d+)([+-])(-{0,1}\d*\.{0,1}\d+)$/;
        const matchArr = str.match(reg);
        const firstNum = parseFloat(matchArr[1]);
        const op = matchArr[2];
        const secondNum = parseFloat(matchArr[3]);
        switch(op) {
            case "+":
                return firstNum + secondNum;
            case "-":
                return firstNum - secondNum;
            default:
                return NaN;
        }
    }

    let partHandler = (str, reg, func) => {
        const part = str.match(reg);
        const newPart = func(part[0]);
        const newStr = str.replace(part, newPart);
        return newStr;
        
    }

    let fixEnd = str;

    while (/\D/.test(fixEnd[fixEnd.length - 1])) {
        fixEnd = fixEnd.slice(0, fixEnd.length - 1);
    }
    
    let spaceless = despace(fixEnd);
    const mdRegex = /-{0,1}\d*\.{0,1}\d+[*/]-{0,1}\d*\.{0,1}\d+/;
    const asRegex = /-{0,1}\d*\.{0,1}\d+[+-]-{0,1}\d*\.{0,1}\d+/;
    const mdReg = /[*/]/g;
    const addReg = /\+/g;
    const subtractReg = /\d-/g;
    const md = spaceless.match(mdReg);
    const add = spaceless.match(addReg);
    const subtract = spaceless.match(subtractReg);
    let numMD = 0;
    let numAS = 0;
    if (md !== null) {
        numMD += md.length
    }
    if (add !== null) {
        numAS += add.length
    } 
    if (subtract !== null) {
        numAS += subtract.length
    }  
    for (let i = 0; i < numMD; i++) {
        spaceless = partHandler(spaceless, mdRegex, mdSolver);
    }



    for (let i = 0; i < numAS; i++) {
        spaceless = partHandler(spaceless, asRegex, asSolver);
    }
    
    return spaceless;
}

export default formula;