import React, { Component } from 'react';
import { connect } from 'react-redux';
import * as mode from '../redux/actions/mode';
import propTypes from 'prop-types';

class Mode extends Component {

    handleClick = (e) => {
        this.props.change(e);
        if (e.target.name === "immediate") {
            this.props.immediate();
        } else if (e.target.name === "formula") {
            this.props.formula();
        }
    }

    render() {
        const mode = this.props.mode;
        let classN = "modei";
        mode === "formula" ? classN = "modef" : classN = "modei";
        return (
            <div id="mode-bar">
                <span>
                    <button type="button" name="immediate" onClick={this.handleClick} className={classN} id="i"></button>
                    <label for="immediate">Immediate</label>
                </span>
                <span>
                    <button type="button" name="formula" onClick={this.handleClick} className={classN} id="f"></button>
                    <label for="formula">Formula</label>
                </span>
            </div>
        )
    }
}

Mode.propTypes = {change: propTypes.func.isRequired};

const mapStateToProps = (state) => ({
    mode: state.mode.mode
})

const modeActions = {
    formula: mode.formulaA,
    immediate: mode.immediateA
}

export default connect(mapStateToProps, modeActions)(Mode);
